import React from "react";
import Title from "../components/Title";
import ItemPorfolio from "../components/ItemPorfolio";

const Works = [
  {
    name: "Jharm Wat",
    type: "Android Application",
    image: "/assets/image/work/m-1.jpg",
    link: "https://play.google.com/store/apps/details?id=ict.twenty.jharmwat&hl=th"
  },
  {
    name: "Mome (Dynamic form)",
    type: "Android Application",
    image: "/assets/image/work/m-2.jpg",
    link: null
  },
  {
    name: "KKU Meeting",
    type: "Android Application",
    image: "/assets/image/work/m-3.jpg",
    link: "https://play.google.com/store/apps/details?id=th.ac.kku.council"
  },
  {
    name: "Smart hotel",
    type: "React Native",
    image: "/assets/image/work/m-4.jpg",
    link: null
  },
  {
    name: "Covid 19 tracker Thailand",
    type: "Android Application",
    image: "/assets/image/work/m-5.jpg",
    link: "https://drive.google.com/file/d/1Q0dGXBR7jab98DZfqzDQDD2-tstnSFJn/view?fbclid=IwAR14gDB3BgrNCSjKC5d1oigsbWAuAjGXRbMja3Sv2kB4J8TfsviDIPdtJ10"
  },
  {
    name: "Marginframe",
    type: "React.js",
    image: "/assets/image/work/w-1.jpg",
    link: "https://www.marginframe.com"    
  },
];

export default function Portfolio() {
  return (
    <div className="flex flex-col my-6">
      <div
        data-aos="fade-up"
        className="flex flex-row justify-center pt-2 pb-4"
      >
        <Title text="Portfolio" />
      </div>
      <div className="flex grid flex-row flex-wrap grid-cols-1 gap-3 lg:grid-cols-2">
        {Works.map((work, index) => {
          if (index % 2 !== 0) {
            return (
              <div  className="block" data-aos="fade-left" key={index}>
                <ItemPorfolio  work={work} />
              </div>
            );
          } else {
            return (
              <div className="block" data-aos="fade-right" key={index}>
                <ItemPorfolio work={work} />
              </div>
            );
          }
        })}
      </div>
    </div>
  );
}
